// A mock function to mimic making an async request for data
export function fetchData() {
  return new Promise((resolve) =>
    setTimeout(() => resolve({
      data: [{
        id:0,
        count:'2',
        item:''
      } , {
        id:1,
        count:'4',
        item:''
      },{
        id:2,
        count:'2',
        item:''
      } , {
        id:3,
        count:'5',
        item:''
      }]
    }), 500)
  );
}
